import { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import axios from "axios";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "25ch"
    }
  }
}));

export default function View() {
  const classes = useStyles();
  const [messageId, setMessageId] = useState();

  const handleSubmit = (event) => {
    event.preventDefault();

    axios
      .get(`https://secret-be.herokuapp.com/message-by-id/${messageId}`)
      .then((res) => {
        if (res.status === 200) {
          // console.log(res.data.result[0].message);
          alert("Secret Message : " + res.data.result[0].message);
        }
      })
      .catch((err) => alert("Error code:" + err));
  };

  return (
    <>
      <h1 className="viewhead">View Secret Message </h1>

      <br />
      <form
        className={classes.root}
        noValidate
        autoComplete="off"
        onSubmit={handleSubmit}
      >
        <TextField
          id="standard-basic"
          label="Message Id"
          name="messageId"
          color="primary"
          onChange={(e) => setMessageId(e.target.value)}
        />
        <br />

        <br />
        <Button variant="contained" color="primary" type="submit">
          View Message
        </Button>
      </form>
    </>
  );
}
