import { BrowserRouter as Router, Switch, Link, Route } from "react-router-dom";
import Home from "./Home";
import Create from "./Create";
import View from "./View";
import Delete from "./Delete";

export default function Routes() {
  return (
    <>
      <Router>
        <div>
          <nav>
            <ul>
              <li>
                <Link to="/">Home</Link>
              </li>
              <br />
              <li>
                <Link to="/create">Create Secret Message</Link>
              </li>
              <br />
              <li>
                <Link to="/view">View Secret Message</Link>
              </li>
              <br />
              <li>
                <Link to="/delete">Delete Secret Message</Link>
              </li>
            </ul>
          </nav>
          <hr />

          <Switch>
            <Route path="/create">
              <Create />
            </Route>
            <Route path="/view">
              <View />
            </Route>
            <Route path="/delete">
              <Delete />
            </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </div>
      </Router>
    </>
  );
}
