import { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import axios from "axios";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "25ch"
    }
  }
}));

export default function Create() {
  const classes = useStyles();
  const [randomkey, setRandomkey] = useState();
  const [message, setMessage] = useState();
  const [password, setPassword] = useState();
  const [targeturl, setTargetUrl] = useState();
  const [targetmail, setTargetMail] = useState();

  const handleSubmit = (event) => {
    event.preventDefault();
    let postdata = {
      randomKey: randomkey,
      message: message,
      password: password,
      targetUrl: targeturl,
      targetMail: targetmail
    };

    axios
      .post("https://secret-be.herokuapp.com/create-message", postdata)
      .then((res) => {
        if (res.status === 200) {
          alert("Message created Successfully");
        }
      });
  };
  return (
    <>
      <h1 className="createhead">Create Secret Message</h1>

      <br />
      <form
        className={classes.root}
        noValidate
        autoComplete="off"
        onSubmit={handleSubmit}
      >
        <TextField
          id="standard-basic"
          label="Random Key"
          name="randomKey"
          color="primary"
          onChange={(e) => setRandomkey(e.target.value)}
        />
        <br />
        <TextField
          id="standard-multiline-flexible"
          label="Message"
          multiline
          rowsMax={4}
          onChange={(e) => setMessage(e.target.value)}
        />
        <br />
        <TextField
          id="standard-password-input"
          name="password"
          label="Password"
          type="password"
          autoComplete="current-password"
          onChange={(e) => setPassword(e.target.value)}
        />
        <br />
        <TextField
          id="standard-basic"
          label="Target URL"
          name="targetUrl"
          color="primary"
          onChange={(e) => setTargetUrl(e.target.value)}
        />
        <br />
        <TextField
          id="standard-basic"
          label="Target Mail"
          name="targetMail"
          color="primary"
          onChange={(e) => setTargetMail(e.target.value)}
        />
        <br />
        <Button variant="contained" color="primary" type="submit">
          Send Message
        </Button>
      </form>
    </>
  );
}
